"use client"

import type React from "react"

import { useState } from "react"
import { Lock } from "lucide-react"
import { Header } from "./header"

interface LoginPageProps {
  onLogin: () => void
}

export function LoginPage({ onLogin }: LoginPageProps) {
  const [email, setEmail] = useState("")
  const [password, setPassword] = useState("")

  const handleSubmit = (e: React.FormEvent) => {
    e.preventDefault()
    onLogin()
  }

  return (
    <div className="min-h-screen bg-slate-100">
      <Header showLogin />

      <main className="flex items-center justify-center py-20">
        <div className="w-full max-w-md bg-white rounded-lg shadow-sm border border-slate-200 p-8">
          <div className="flex flex-col items-center mb-6">
            <div className="w-12 h-12 bg-slate-100 rounded-full flex items-center justify-center mb-3">
              <Lock className="w-5 h-5 text-slate-600" />
            </div>
            <h2 className="text-xl font-medium text-slate-800">Sign in</h2>
          </div>

          <form onSubmit={handleSubmit} className="space-y-4">
            <div>
              <label className="block text-sm text-slate-600 mb-1">
                Email Address <span className="text-red-500">*</span>
              </label>
              <input
                type="email"
                value={email}
                onChange={(e) => setEmail(e.target.value)}
                className="w-full px-3 py-2 border border-slate-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-sm"
                required
              />
            </div>

            <div>
              <label className="block text-sm text-slate-600 mb-1">
                Password <span className="text-red-500">*</span>
              </label>
              <input
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                className="w-full px-3 py-2 border border-slate-300 rounded focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent text-sm"
                required
              />
            </div>

            <button
              type="submit"
              className="w-full bg-blue-600 text-white py-2 rounded font-medium hover:bg-blue-700 transition-colors text-sm"
            >
              Sign In
            </button>
          </form>

          <div className="flex justify-between mt-4 text-xs">
            <a href="#" className="text-blue-600 hover:underline">
              Forgot password?
            </a>
            <a href="#" className="text-blue-600 hover:underline">
              Don't have an account? Sign Up
            </a>
          </div>
        </div>
      </main>
    </div>
  )
}
